* {
	border: 0;
}

.heading {
	height: 150px;
	background-color: #0099FF;
	color: #fff;
}

.heading h4 {
	border-bottom: 1.5px solid skyblue;
	padding-bottom: 23px;
	margin: 0;
	margin-bottom: 20px;
}

.elem {
	display: inline-block;
	text-align: center;
	margin-left: 30px;
	margin-right: 30px;
	margin-bottom: 20px;
}

.ico img {
	width: 50px;
	color: white;
	margin: auto;
}

.card:first-child {
	width: 350px; 
	height: 150px; 
	overflow: hidden; 
	background-color: #0099FF; 
	margin: auto; 
	padding: 25px
}

.card:last-child {
	width: 350px; 
	height: 300px;
	overflow: hidden; 
	background-color: #fff; 
	margin: auto; 
	padding: 25px
}

.head {
	font-size: 20px;
	padding-bottom: 10px;
	border-bottom: 2px solid orange;
	width: 100px;
	text-align: center;
	z-index: 100;
}

.headBorder {
	height: 34.4px;
	width: 100%;
	border-bottom: 2px solid lightgray;
	z-index: -100;
	margin-bottom: 30px;
}

.time, .inline {
	display: inline-block;
	height: 51px;
}

.time {
	width: 262px;
}

section {
	padding-bottom: 10px;
}

.time p {
	color: #8A8B8D;
	font-weight: 900;
}

.temp {
	font-weight: 900;
}

.status {
	color: #8A8B8D
}


